.button {
  position: fixed;
  left: 50%;
  bottom: 0px;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 26px;
  color: white;
  background-color: black;
  border: 2px solid black;
  padding: 6px 24px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  transform: translateX(-50%);
  opacity: 0;
  transition: 0.3s;
  cursor: pointer;
  z-index: 5;

  &:hover {
    color: black;
    background-color: white;
    border: 2px solid black;
    border-bottom: 2px solid white;
    transition: 0.3s;
  }

  &_hidden {
    transform: translate(-50%, 42px);
    transition: 0.3s;
  }
}

@media screen and (max-width: 768px) {
  .button {
    width: max-content;
    padding: 6px 12px;
  }
}
